@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(50%); }
  40% {
    opacity: 1;
    transform: translateY(50%); }
  50% {
    opacity: 1;
    transform: translateY(50%); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes slideInLeft {
  0% {
    transform: translate3d(-100%, 0, 0) skewX(30deg);
    opacity: 0; }
  70% {
    transform: translate3d(0, 0, 0) skewX(10deg);
