        min-width: 40%; } }

.balloon__balloonarea-box {
  position: relative;
  display: inline-block;
  margin-left: 40px;
  padding: .5rem 1rem;
  max-width: calc(100% - 40px);
  min-width: 120px;
  background: #FFF;
  border: solid 3px #555;
  box-sizing: border-box; }
  .balloon__balloonarea-box:before {
    content: "";
    position: absolute;
    top: 50%;
    right: calc(100% - 3px);
    margin-top: -12px;
    border: 12px solid transparent;
    border-right: 12px solid #FFF;
    z-index: 2; }
  .balloon__balloonarea-box:after {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    margin-top: -14px;
    border: 14px solid transparent;
    border-right: 14px solid #555;
    z-index: 1; }
  .balloon__balloonarea-box .balloon--left {
    margin-left: 0px;
    margin-right: 40px; }
    .balloon__balloonarea-box .balloon--left:before {
      right: initial;
      left: calc(100% - 3px);
      border-left: 12px solid #FFF; }
    .balloon__balloonarea-box .balloon--left:after {
      right: initial;
      left: 100%;
      border: 14px solid transparent;
      border-left: 14px solid #555; }
  .balloon--top .balloon__balloonarea-box {
    min-height: 161px; }
    .balloon--top .balloon__balloonarea-box:before {
      top: 50px; }
    .balloon--top .balloon__balloonarea-box:after {
      top: 50px; }
  .balloon--sp-top .balloon__balloonarea-box {
    /*全スマホ*/ }
