      .balloon--sp-top .balloon__balloonarea-box {
        min-height: 84px; } }
    .balloon--sp-top .balloon__balloonarea-box:before {
      /*全スマホ*/ }
      @media only screen and (max-width: 40em) {
        .balloon--sp-top .balloon__balloonarea-box:before {
          top: 40px; } }
    .balloon--sp-top .balloon__balloonarea-box:after {
      /*全スマホ*/ }
      @media only screen and (max-width: 40em) {
        .balloon--sp-top .balloon__balloonarea-box:after {
          top: 40px; } }
  .balloon__balloonarea--sp-60 .balloon__balloonarea-box {
    /*全スマホ*/ }
